@font-face {
  font-family: Clip;
  src: url("https://acupoftee.github.io/fonts/Clip.ttf");
}

.body {
  font-size: 7px; /* Adjusted for better readability */
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh; 
 
 margin-left: 150px;
 margin-top: 10px;
  background: #000; /* Optional: Add a background for contrast */
}

.sign {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: radial-gradient(
    ellipse 50% 35% at 50% 50%,
    #6b1839,
    transparent
  );
  transform: translate(-50%, -50%);
  letter-spacing: 2;
 
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 6em;
  color: #ffe6ff;
  text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #65f2ff,
    -0.2rem 0.1rem 1rem #65faff, 0.2rem 0.1rem 1rem #65e5ff,
    0 -0.5rem 2rem #24daff, 0 0.5rem 3rem #24f0ff;
  animation: shine 2s forwards, flicker 3s infinite;
}

@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #65d8ff,
      -0.2rem 0.1rem 1rem #65ceff, 0.2rem 0.1rem 1rem #65e0ff,
      0 -0.5rem 2rem #24e9ff, 0 0.5rem 3rem #24ccff;
  }
  28%,
  33% {
    color: #65c7ff;
    text-shadow: none;
  }
  82%,
  97% {
    color: #24baff;
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
  animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
  0% {
    color: #6b1839;
    text-shadow: none;
  }
  100% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #65d8ff,
      -0.2rem 0.1rem 1rem #65c4ff, 0.2rem 0.1rem 1rem #65bcff,
      0 -0.5rem 2rem #24afff, 0 0.5rem 3rem #2499ff;
  }
}

@keyframes flicker {
  from {
    opacity: 1;
  }

  4% {
    opacity: 0.9;
  }

  6% {
    opacity: 0.85;
  }

  8% {
    opacity: 0.95;
  }

  10% {
    opacity: 0.9;
  }

  11% {
    opacity: 0.922;
  }

  12% {
    opacity: 0.9;
  }

  14% {
    opacity: 0.95;
  }

  16% {
    opacity: 0.98;
  }

  17% {
    opacity: 0.9;
  }

  19% {
    opacity: 0.93;
  }

  20% {
    opacity: 0.99;
  }

  24% {
    opacity: 1;
  }

  26% {
    opacity: 0.94;
  }

  28% {
    opacity: 0.98;
  }

  37% {
    opacity: 0.93;
  }

  38% {
    opacity: 0.5;
  }

  39% {
    opacity: 0.96;
  }

  42% {
    opacity: 1;
  }

  44% {
    opacity: 0.97;
  }

  46% {
    opacity: 0.94;
  }

  56% {
    opacity: 0.9;
  }

  58% {
    opacity: 0.9;
  }

  60% {
    opacity: 0.99;
  }

  68% {
    opacity: 1;
  }

  70% {
    opacity: 0.9;
  }

  72% {
    opacity: 0.95;
  }

  93% {
    opacity: 0.93;
  }

  95% {
    opacity: 0.95;
  }

  97% {
    opacity: 0.93;
  }

  to {
    opacity: 1;
  }
}


/* Media Queries for responsiveness */

/* For tablet and smaller screens */
@media (max-width: 768px) {
  .sign {
    font-size: 4em; /* Adjust font size for tablets */
  }
}

/* For mobile screens */
@media (max-width: 480px) {
  .sign {
    font-size: 2.5em; /* Adjust font size for mobile devices */
    letter-spacing: 1px; /* Adjust letter spacing for smaller screens */
  }

  .body {
    height: 15vh; /* Make the body height adjust for smaller screens */
  }
}

@media (max-width: 360px) {
  .sign {
    font-size: 4em; /* Further reduce font size for very small screens */
  }
}

/* Hover effect */
.sign:hover {
  cursor: pointer;
}